.info-communication {
  max-width: 784px;
  display: flex;
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;

  @include vp-1023 {
    flex-wrap: wrap;
    justify-content: end;
    align-items: baseline;
  }
}

.info-communication__list {
  margin: 0;
  padding: 0;
  max-width: 800px;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;

  @include vp-1023 {
    justify-content: end;
  }
}

.info-communication__item {
  width: 225px;
  margin-right: 30px;

  @include vp-1023 {
    width: 40px;
    margin-right: 20px;
  }
}

.info-communication__link {
  display: inline-block;
  margin: 15px auto 15px 0;
  font-family: $ff-pt-mono;
  font-size: 16px;
  line-height: 1.13;
  color: #f2f5f7;

  @include hover {
    outline: 1px solid $color-default-white;
  }

  @include vp-1023 {
    margin: 0;
    padding: 10px 0;
    justify-content: end;
  }

  @include vp-767 {
    display: flex;
    width: 100%;
    padding: 4px 0;
  }
}

.info-communication__link span {
  vertical-align: top;

  @include vp-1023 {
    font-size: 0;
  }
}

.info-communication__icon {
  margin-right: 6px;
}

.info-communication__order {
  @include vp-1023 {
    display: none;
  }
}
